.App {
  text-align: center;
  align-items: center;
}

.userInfoForm{
  width: 340px;
  margin: auto;
  align-items: center;
  display: grid;
  grid-template-rows: 30px 30px 30px; 
}

.Input-Row{
  align-items: center;
  display: grid;
  grid-template-columns: 70px 250px; 
}

.App-header {
  background-color: #282c34;
  min-height: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
