.Question {
    margin-top: 10px;
    background-color: #282c34;
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 1vmin);
    color: white;

}

.Container {
    margin-top: 1vmin;
    margin-bottom: 3vmin;
    height: 30 vmin;
    display: flex;
    flex-direction: row;
}
.Target-FV {
    height: 30vmin;
}

.Option-box {
    height: 30vmin;
    width: 30vmin;
    background-color: #dee2e9;
}

.clicked {
    stroke: red;
    stroke-width: 3px;
}